// components/dynamic-zone/how-it-works/index.tsx
'use client';

import {
  type AnimationPlaybackControls,
  animate,
  motion,
  useMotionValue,
  useMotionValueEvent,
  useReducedMotion,
  useTransform,
} from 'framer-motion';
import {
  Calendar,
  ChevronLeft,
  ChevronRight,
  type LucideIcon,
  MessageCircle,
  Paintbrush,
  Scissors,
} from 'lucide-react';
import React, { useEffect, useMemo, useRef, useState } from 'react';

import { Container } from '../../container';
import { Heading } from '../../elements/heading';
import { Subheading } from '../../elements/subheading';

type StepInput = {
  title?: string | null;
  description?: string | null;
  ctaText?: string | null;
  ctaHref?: string | null;
};

type Step = {
  title: string;
  description: string;
  ctaText?: string;
  ctaHref?: string;
  icon: LucideIcon;
};

// -----------------------------
// Easy params (tweak here)
// -----------------------------
const DIAL = {
  GOLD: '#B2933D',

  // Geometry
  ARC_H: { mobile: 420, desktop: 470 },
  PEAK_Y: { mobile: 132, desktop: 148 },

  // Circle size/radius (keeps arc perfectly circular: width===height===size)
  RADIUS: {
    mobile: { scale: 0.86, min: 460, max: 640 },
    desktop: { scale: 0.78, min: 560, max: 820 },
  },

  // Step distribution along the arc (wide enough so all remain visible)
  STEP_SPAN: { mobile: 220, desktop: 244 },

  // Ticks (minor/mid/major pattern stays many & grey)
  TICK_START: -180,
  TICK_SPAN: 360,
  TICK_COUNT: { mobile: 132, desktop: 176 },
  MID_EVERY: { mobile: 6, desktop: 8 },
  MAJOR_EVERY: { mobile: 13, desktop: 16 },
  TICK_LEN: {
    minor: { mobile: 20, desktop: 24 },
    mid: { mobile: 34, desktop: 42 },
    major: { mobile: 48, desktop: 60 },
  },

  // Major tick highlight (step ticks)
  THRESHOLD_DEG: 18, // highlight falloff window around center
  MAJOR_TICK_MIN_LEN: { mobile: 140, desktop: 160 }, // “reach” to center focus baseline

  // Step nodes feel
  STEP_FOCUS_DEG: 125, // fade/scale across arc
  COUNTER_FACTOR: 0.88, // keep nodes mostly vertical

  // Drag speed (smaller => slower / more visible)
  DEG_PER_PX: { mobile: 0.095, desktop: 0.075 },

  // Snap feel (premium, slower)
  SPRING: { stiffness: 150, damping: 24, mass: 1.0 },
  SOFT: { duration: 0.32, ease: [0.22, 1, 0.36, 1] as const },

  // Auto loop (4.8–5.5s jitter)
  AUTO_MS: { min: 4800, max: 5500 },
  AUTO_PAUSE_MS: 11000,

  // Center marker vertical layout
  CENTER: {
    labelOffsetY: { mobile: 46, desktop: 50 },
    iconOffsetY: { mobile: 176, desktop: 196 },
    titleOffsetY: { mobile: 30, desktop: 32 },
  },
} as const;

// -----------------------------
// Utils
// -----------------------------
const clamp = (n: number, min: number, max: number) =>
  Math.max(min, Math.min(max, n));
const pad2 = (n: number) => String(n).padStart(2, '0');

const normDeg = (d: number) => {
  const x = ((((d + 180) % 360) + 360) % 360) - 180;
  return x === -180 ? 180 : x;
};

const closestRotation = (current: number, target: number) => {
  // Find target equivalent (target + 360k) closest to current
  const diff = normDeg(current - target);
  return current - diff;
};

const closestRotationDirectional = (
  current: number,
  target: number,
  dir: -1 | 0 | 1
) => {
  // dir: +1 => continue “forward” (rot tends to decrease for next),
  //      -1 => continue “backward” (rot tends to increase for prev),
  //       0 => shortest path.
  let c = closestRotation(current, target);
  if (dir === 1 && c > current) c -= 360;
  if (dir === -1 && c < current) c += 360;
  return c;
};

const lerp = (a: number, b: number, t: number) => a + (b - a) * t;
const rgba = (r: number, g: number, b: number, a: number) =>
  `rgba(${Math.round(r)}, ${Math.round(g)}, ${Math.round(b)}, ${a})`;

function TogglePill({
  value,
  onChange,
  reduce,
}: {
  value: boolean;
  onChange: (v: boolean) => void;
  reduce: boolean;
}) {
  return (
    <div className="mt-4 inline-flex rounded-full bg-white/10 p-1 shadow-[inset_0_0_0_1px_rgba(255,255,255,0.06)]">
      <div className="relative flex">
        <motion.span
          aria-hidden
          className="absolute bottom-0 top-0 rounded-full bg-white/15 shadow-[0_12px_30px_rgba(0,0,0,0.35),inset_0_0_0_1px_rgba(255,255,255,0.08)]"
          animate={{ left: value ? 0 : '50%', width: '50%' }}
          transition={
            reduce
              ? { duration: 0 }
              : { type: 'spring', stiffness: 420, damping: 34 }
          }
        />
        <button
          type="button"
          onClick={() => onChange(true)}
          className={[
            'relative z-10 rounded-full px-6 py-2 text-sm',
            'text-white/85 transition',
            'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
            value ? 'text-white' : 'hover:text-white',
          ].join(' ')}
        >
          Igen
        </button>
        <button
          type="button"
          onClick={() => onChange(false)}
          className={[
            'relative z-10 rounded-full px-6 py-2 text-sm',
            'text-white/85 transition',
            'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
            !value ? 'text-white' : 'hover:text-white',
          ].join(' ')}
        >
          Nem
        </button>
      </div>
    </div>
  );
}

function Tick({
  a,
  radius,
  len,
  opacity,
}: {
  a: number; // deg
  radius: number; // px
  len: number; // px
  opacity: number;
}) {
  // placed on circle via rotate(a) translateY(-radius)
  // counter-rotate the line by -(dial + a) so it stays vertical in world-space
  return (
    <div
      aria-hidden
      className="absolute left-1/2 top-1/2"
      style={{
        transform: `translate(-50%, -50%) rotate(${a}deg) translateY(-${radius}px)`,
      }}
    >
      <span
        className="absolute left-0 top-0 w-px bg-white"
        style={{
          height: len,
          opacity,
          transformOrigin: 'top center',
          transform: `translateX(-0.5px) rotate(calc(0deg - var(--dial) - ${a}deg))`,
        }}
      />
    </div>
  );
}

function MajorTick({
  angleDeg,
  radius,
  len,
  rot,
  thresholdDeg,
}: {
  angleDeg: number;
  radius: number;
  len: number;
  rot: ReturnType<typeof useMotionValue<number>>;
  thresholdDeg: number;
}) {
  // dist = abs(normDeg(rot + angleDeg))
  const dist = useTransform(rot, (r) => Math.abs(normDeg(r + angleDeg)));
  const t = useTransform(dist, (d) => clamp(1 - d / thresholdDeg, 0, 1));

  const w = useTransform(t, (x) => `${lerp(1, 2.6, x)}px`);

  const bg = useTransform(t, (x) => {
    // grey -> gold
    const r = lerp(255, 178, x);
    const g = lerp(255, 147, x);
    const b = lerp(255, 61, x);
    const a = lerp(0.22, 0.98, x);
    // slight “line” look: fade at ends
    return `linear-gradient(to bottom, ${rgba(r, g, b, 0)}, ${rgba(
      r,
      g,
      b,
      a
    )}, ${rgba(r, g, b, a * 0.16)})`;
  });

  const shadow = useTransform(t, (x) => {
    const a = lerp(0.08, 0.55, x);
    const a2 = lerp(0.02, 0.24, x);
    return `drop-shadow(0 18px 26px rgba(0,0,0,0.32)) drop-shadow(0 16px 26px rgba(178,147,61,${a})) drop-shadow(0 2px 10px rgba(178,147,61,${a2}))`;
  });

  const op = useTransform(t, (x) => lerp(0.78, 1, x));

  return (
    <div
      aria-hidden
      className="pointer-events-none absolute left-1/2 top-1/2"
      style={{
        transform: `translate(-50%, -50%) rotate(${angleDeg}deg) translateY(-${radius}px)`,
      }}
    >
      <motion.span
        className="absolute left-0 top-0"
        style={{
          height: len,
          width: w,
          opacity: op,
          backgroundImage: bg,
          transformOrigin: 'top center',
          transform: `translateX(-0.5px) rotate(calc(0deg - var(--dial) - ${angleDeg}deg))`,
          filter: shadow,
          borderRadius: 999,
        }}
      />
    </div>
  );
}

function StepNode({
  angleDeg,
  radius,
  stemLen,
  step,
  index,
  rot,
  reduce,
  isMobile,
  onPick,
}: {
  angleDeg: number;
  radius: number;
  stemLen: number;
  step: Step;
  index: number;
  rot: ReturnType<typeof useMotionValue<number>>;
  reduce: boolean;
  isMobile: boolean;
  onPick: () => void;
}) {
  const Icon = step.icon;

  const counter = useTransform(rot, (r) => -(r + angleDeg) * DIAL.COUNTER_FACTOR);

  // distance from center
  const dist = useTransform(rot, (r) => Math.abs(normDeg(r + angleDeg)));
  const focusT = useTransform(dist, (d) =>
    clamp(1 - d / DIAL.STEP_FOCUS_DEG, 0, 1)
  );

  const scale = useTransform(focusT, (t) => lerp(0.9, 1.12, t));
  const opacity = useTransform(focusT, (t) => lerp(0.28, 1, t));

  const iconOpacity = useTransform(focusT, (t) => lerp(0.42, 0.92, t));
  const labelOpacity = useTransform(focusT, (t) => lerp(0.22, 0.9, t));

  const iconSize = isMobile ? 26 : 30;

  return (
    <div
      className="absolute left-1/2 top-1/2"
      style={{
        transform: `translate(-50%, -50%) rotate(${angleDeg}deg) translateY(-${radius}px)`,
      }}
    >
      <motion.div
        style={{
          rotate: counter,
          scale,
          opacity,
          transformOrigin: '50% 0%',
        }}
        className="relative"
      >
        {/* stem */}
        <div
          aria-hidden
          className="absolute left-1/2 top-0 w-[2px]"
          style={{
            height: stemLen,
            transform: 'translateX(-1px)',
            background:
              'linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0.26), rgba(255,255,255,0.10))',
            filter: 'drop-shadow(0 14px 22px rgba(0,0,0,0.28))',
          }}
        />

        {/* icon + short title */}
        <motion.button
          type="button"
          onClick={onPick}
          className={[
            'absolute left-1/2',
            'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
          ].join(' ')}
          style={{
            top: stemLen,
            transform: 'translate(-50%, -50%)',
            padding: isMobile ? 10 : 12, // big click target
            background: 'transparent',
          }}
          whileHover={reduce ? undefined : { y: -1, scale: 1.02 }}
          whileTap={reduce ? undefined : { y: 0, scale: 0.985 }}
          transition={
            reduce ? undefined : { type: 'spring', stiffness: 320, damping: 24 }
          }
          aria-label={`${pad2(index + 1)} – ${step.title}`}
        >
          <div className="flex flex-col items-center">
            <Icon
              aria-hidden
              className="text-white"
              style={{
                width: iconSize,
                height: iconSize,
                opacity: iconOpacity.get(), // initial; motion will override below
              }}
            />
            <motion.div
              className={[
                'mt-2',
                'max-w-[128px]',
                'truncate',
                'text-[11px] font-medium leading-none tracking-tight',
                'text-white/80',
              ].join(' ')}
              style={{ opacity: labelOpacity }}
            >
              {step.title}
            </motion.div>
          </div>

          {/* drive icon opacity via motion without re-render */}
          <motion.span
            aria-hidden
            className="pointer-events-none absolute inset-0"
            style={{ opacity: iconOpacity }}
          />
        </motion.button>

        {/* Keep Icon opacity actually animated (avoids relying on get()) */}
        <motion.div
          aria-hidden
          className="pointer-events-none absolute left-1/2"
          style={{
            top: stemLen,
            transform: 'translate(-50%, -50%)',
            opacity: iconOpacity,
          }}
        >
          <Icon
            className="text-white"
            style={{
              width: iconSize,
              height: iconSize,
            }}
          />
        </motion.div>
      </motion.div>
    </div>
  );
}

export const HowItWorks = ({
  heading,
  sub_heading,
  steps,
}: {
  heading?: string | null;
  sub_heading?: string | null;
  steps?: StepInput[] | null;
}) => {
  const reduce = useReducedMotion();

  const fallback: Omit<Step, 'icon'>[] = useMemo(
    () => [
      {
        title: 'Időpont foglalás',
        description:
          'Kezdje meg útját a megújulás felé. Válassza ki az Önnek legmegfelelőbb időpontot kényelmes online rendszerünkön keresztül, vagy hívjon minket bizalommal egyedi igényeivel.',
        ctaText: 'Foglalás most',
        ctaHref: '#',
      },
      {
        title: 'Konzultáció',
        description:
          'Személyes találkozó szakértőinkkel, ahol részletesen felmérjük igényeit, hajtípusát és stílusát. Egy közös beszélgetés során megalkotjuk az Önre szabott, egyedi tervet.',
      },
      {
        title: 'Szolgáltatás',
        description:
          'Dőljön hátra és élvezze a prémium kezelést nyugodt, luxus környezetben. A legkiválóbb anyagok és legmodernebb technikák felhasználásával varázsoljuk újjá megjelenését.',
      },
      {
        title: 'Utóápolás',
        description:
          'A törődés nem ér véget a szalonban. Személyre szabott tanácsokkal és professzionális termék-ajánlással segítünk, hogy az eredmény tartósan ragyogó maradjon.',
        ctaText: 'Kapcsolat',
        ctaHref: '#',
      },
    ],
    []
  );

  const iconByIndex: LucideIcon[] = useMemo(
    () => [Calendar, MessageCircle, Scissors, Paintbrush],
    []
  );

  const resolvedSteps: Step[] = useMemo(() => {
    const src = Array.isArray(steps) && steps.length ? steps : fallback;
    return src.map((s, i) => ({
      title: (s.title ?? fallback[i]?.title ?? `Lépés ${i + 1}`) as string,
      description: (s.description ?? fallback[i]?.description ?? '') as string,
      ctaText: (s as any).ctaText ?? fallback[i]?.ctaText ?? undefined,
      ctaHref: (s as any).ctaHref ?? fallback[i]?.ctaHref ?? undefined,
      icon: iconByIndex[i % iconByIndex.length] ?? Scissors,
    }));
  }, [steps, fallback, iconByIndex]);

  const len = resolvedSteps.length;

  const [active, setActive] = useState(0);
  const [auto, setAuto] = useState<boolean>(() => !reduce);
  const [dragging, setDragging] = useState(false);
  const [settling, setSettling] = useState(false);

  useEffect(() => {
    if (reduce) setAuto(false);
  }, [reduce]);

  const safeActive = clamp(active, 0, Math.max(0, len - 1));

  // ---- panel width (for responsive geometry) ----
  const panelRef = useRef<HTMLDivElement>(null);
  const [vw, setVw] = useState(0);

  useEffect(() => {
    const el = panelRef.current;
    if (!el) return;
    const ro = new ResizeObserver((entries) => {
      const w = entries?.[0]?.contentRect?.width ?? 0;
      setVw(w);
    });
    ro.observe(el);
    return () => ro.disconnect();
  }, []);

  const geom = useMemo(() => {
    const viewportW = Math.max(340, vw || 0);
    const isMobile = viewportW < 560;

    const arcH = isMobile ? DIAL.ARC_H.mobile : DIAL.ARC_H.desktop;

    const rr = isMobile ? DIAL.RADIUS.mobile : DIAL.RADIUS.desktop;
    const radius = clamp(viewportW * rr.scale, rr.min, rr.max);
    const size = radius * 2;

    const peakY = isMobile ? DIAL.PEAK_Y.mobile : DIAL.PEAK_Y.desktop;

    const stepSpan = isMobile ? DIAL.STEP_SPAN.mobile : DIAL.STEP_SPAN.desktop;
    const stepStart = -stepSpan / 2;
    const stepAngles =
      len <= 1
        ? [0]
        : Array.from(
            { length: len },
            (_, i) => stepStart + (i * stepSpan) / (len - 1)
          );

    const tickCount = isMobile ? DIAL.TICK_COUNT.mobile : DIAL.TICK_COUNT.desktop;

    return {
      viewportW,
      isMobile,
      arcH,
      radius,
      size,
      peakY,
      stepAngles,
      tickStart: DIAL.TICK_START,
      tickSpan: DIAL.TICK_SPAN,
      tickCount,
      midEvery: isMobile ? DIAL.MID_EVERY.mobile : DIAL.MID_EVERY.desktop,
      majorEvery: isMobile ? DIAL.MAJOR_EVERY.mobile : DIAL.MAJOR_EVERY.desktop,
      tickLen: {
        minor: isMobile ? DIAL.TICK_LEN.minor.mobile : DIAL.TICK_LEN.minor.desktop,
        mid: isMobile ? DIAL.TICK_LEN.mid.mobile : DIAL.TICK_LEN.mid.desktop,
        major: isMobile ? DIAL.TICK_LEN.major.mobile : DIAL.TICK_LEN.major.desktop,
      },
      majorTickMinLen: isMobile
        ? DIAL.MAJOR_TICK_MIN_LEN.mobile
        : DIAL.MAJOR_TICK_MIN_LEN.desktop,
    };
  }, [vw, len]);

  // ---- rotation (dial) ----
  const rot = useMotionValue(0); // degrees

  const animRef = useRef<AnimationPlaybackControls | null>(null);
  const stopAnim = () => {
    animRef.current?.stop();
    animRef.current = null;
  };

  const pauseUntilRef = useRef(0);
  const isDraggingRef = useRef(false);

  const pauseAuto = (ms = DIAL.AUTO_PAUSE_MS) => {
    pauseUntilRef.current = Date.now() + ms;
  };

  // Center target: chosen step should land at 0°
  const rotTargetForIndex = (i: number) => {
    const ang = geom.stepAngles[i] ?? 0;
    return 0 - ang;
  };

  const snapTo = (i: number, dir: -1 | 0 | 1, soft = false) => {
    const idx = clamp(i, 0, Math.max(0, len - 1));
    const target = rotTargetForIndex(idx);

    stopAnim();

    if (reduce) {
      setSettling(false);
      rot.set(target);
      return;
    }

    const current = rot.get();
    const chosen = closestRotationDirectional(current, target, dir);

    setSettling(true);
    animRef.current = animate(
      rot,
      chosen,
      soft
        ? DIAL.SOFT
        : { type: 'spring', ...DIAL.SPRING, onComplete: () => setSettling(false) }
    );
  };

  // Initial snap on resize (soft)
  useEffect(() => {
    snapTo(safeActive, 0, true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [geom.viewportW]);

  // Live highlight: whichever step is closest to center (0°)
  const [liveActive, setLiveActive] = useState(safeActive);
  const lastLiveRef = useRef(0);

  const nearestIndexByRot = (rotDeg: number) => {
    let best = 0;
    let bestD = Infinity;
    for (let i = 0; i < geom.stepAngles.length; i++) {
      const target = rotTargetForIndex(i);
      const d = Math.abs(normDeg(rotDeg - target));
      if (d < bestD) {
        bestD = d;
        best = i;
      }
    }
    return best;
  };

  useMotionValueEvent(rot, 'change', (latest) => {
    const now = performance.now();
    if (now - lastLiveRef.current < 55) return;
    lastLiveRef.current = now;
    setLiveActive(nearestIndexByRot(latest));
  });

  const displayIndex = dragging || settling ? liveActive : safeActive;
  const displayStep =
    resolvedSteps[clamp(displayIndex, 0, Math.max(0, len - 1))];

  // Go helpers (wrap)
  const goBy = (delta: -1 | 1) => {
    if (len <= 1) return;
    pauseAuto();
    const next = (safeActive + delta + len) % len;
    setActive(next);
    snapTo(next, delta);
  };

  const goTo = (idx: number) => {
    pauseAuto();
    setActive(idx);
    snapTo(idx, 0);
  };

  // Auto-loop with jitter (4.8–5.5s)
  useEffect(() => {
    if (reduce) return;
    if (!auto) return;
    if (len <= 1) return;

    let t: number | null = null;
    let cancelled = false;

    const schedule = () => {
      if (cancelled) return;
      const ms =
        Math.floor(
          lerp(DIAL.AUTO_MS.min, DIAL.AUTO_MS.max, Math.random())
        ) || DIAL.AUTO_MS.min;

      t = window.setTimeout(() => {
        if (cancelled) return;
        if (isDraggingRef.current) {
          schedule();
          return;
        }
        if (Date.now() < pauseUntilRef.current) {
          schedule();
          return;
        }
        const next = (safeActive + 1) % len;
        setActive(next);
        snapTo(next, 1);
        schedule();
      }, ms);
    };

    schedule();

    return () => {
      cancelled = true;
      if (t) window.clearTimeout(t);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [auto, reduce, len, safeActive]);

  // drag → rotation
  const dragStartRotRef = useRef(0);
  const DEG_PER_PX = geom.isMobile ? DIAL.DEG_PER_PX.mobile : DIAL.DEG_PER_PX.desktop;

  const onDragStart = () => {
    pauseAuto();
    isDraggingRef.current = true;
    setDragging(true);
    setSettling(false);
    stopAnim();
    dragStartRotRef.current = rot.get();
  };

  const onDragEnd = () => {
    isDraggingRef.current = false;
    setDragging(false);
    const idx = nearestIndexByRot(rot.get());
    setActive(idx);
    snapTo(idx, 0);
  };

  // keyboard (wrap)
  const onKeyDown: React.KeyboardEventHandler<HTMLDivElement> = (e) => {
    if (e.key === 'ArrowLeft') {
      e.preventDefault();
      goBy(-1);
    }
    if (e.key === 'ArrowRight') {
      e.preventDefault();
      goBy(1);
    }
  };

  const ticks = useMemo(
    () => Array.from({ length: geom.tickCount }, (_, i) => i),
    [geom.tickCount]
  );

  // CSS var for “dial rotation” (used to keep ticks vertical)
  const dialCss = useTransform(rot, (r) => `${r}deg`);

  // Center marker geometry (NO fixed gold line, NO extra gold dot/diamond)
  const marker = useMemo(() => {
    const peak = geom.peakY;
    const iconY =
      peak +
      (geom.isMobile ? DIAL.CENTER.iconOffsetY.mobile : DIAL.CENTER.iconOffsetY.desktop);

    return {
      labelY: peak - (geom.isMobile ? DIAL.CENTER.labelOffsetY.mobile : DIAL.CENTER.labelOffsetY.desktop),
      iconY,
      titleY: iconY + (geom.isMobile ? DIAL.CENTER.titleOffsetY.mobile : DIAL.CENTER.titleOffsetY.desktop),
    };
  }, [geom.isMobile, geom.peakY]);

  // Major tick length: long enough to “meet” the center marker visually
  const majorTickLen = useMemo(() => {
    const desired = Math.max(0, marker.iconY - geom.peakY - 10);
    return Math.max(desired, geom.majorTickMinLen);
  }, [marker.iconY, geom.peakY, geom.majorTickMinLen]);

  return (
    <section className="relative">
      <Container className="mx-auto max-w-7xl py-20 md:py-28">
        {(heading || sub_heading) && (
          <header className="mx-auto mb-10 max-w-4xl text-center">
            {heading ? (
              <Heading className="text-center">{heading}</Heading>
            ) : null}
            {sub_heading ? (
              <Subheading className="mx-auto mt-4 max-w-2xl text-center">
                {sub_heading}
              </Subheading>
            ) : null}
          </header>
        )}

        <div
          ref={panelRef}
          tabIndex={0}
          onKeyDown={onKeyDown}
          className={[
            'relative overflow-hidden rounded-[28px] md:rounded-[32px]',
            'border border-white/10',
            'bg-[#0b0c0e]',
            'outline-none',
            'shadow-[0_40px_140px_rgba(0,0,0,0.55)]',
            'min-h-[610px] md:min-h-[710px]',
          ].join(' ')}
        >
          {/* vignette + lights (TILOS változtatni) */}
          <div
            aria-hidden
            className="pointer-events-none absolute inset-0"
            style={{
              backgroundImage:
                'radial-gradient(900px circle at 20% 12%, rgba(255,255,255,0.10), transparent 58%), radial-gradient(820px circle at 78% 18%, rgba(255,255,255,0.08), transparent 62%), radial-gradient(1200px circle at 50% 120%, rgba(0,0,0,0.88), transparent 58%), radial-gradient(1200px circle at 50% 50%, rgba(0,0,0,0.55), transparent 70%)',
            }}
          />

          {/* subtle grain (TILOS változtatni) */}
          <div
            aria-hidden
            className="pointer-events-none absolute inset-0 opacity-[0.10] mix-blend-overlay"
          >
            <svg
              className="h-full w-full"
              preserveAspectRatio="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <filter id="noiseFilter">
                <feTurbulence
                  type="fractalNoise"
                  baseFrequency="0.9"
                  numOctaves="3"
                  stitchTiles="stitch"
                />
              </filter>
              <rect
                width="100%"
                height="100%"
                filter="url(#noiseFilter)"
                opacity="0.6"
              />
            </svg>
          </div>

          {/* content */}
          <div className="relative z-10 flex h-full flex-col items-center px-6 py-10 md:px-10 md:py-12">
            {/* top center: auto-loop */}
            <div className="mt-2 text-center">
              <div className="text-sm font-medium text-white/85">
                Automatikus váltás
              </div>

              <TogglePill
                reduce={!!reduce}
                value={auto}
                onChange={(v) => {
                  pauseAuto(2500);
                  setAuto(v);
                }}
              />

              <div className="mt-6 max-w-xl text-center text-sm leading-relaxed text-white/55">
                Kattints az ikonokra, vagy húzd az ívet – billentyűzeten a
                nyilakkal is navigálhatsz.
              </div>
            </div>

            {/* dial + content */}
            <div className="relative mt-10 w-full flex-1">
              {/* arc stage */}
              <div
                className="relative mx-auto w-full overflow-hidden"
                style={{ height: geom.arcH }}
              >
                {/* DRAG LAYER */}
                <motion.div
                  aria-hidden
                  className="absolute inset-0 touch-pan-y"
                  drag={reduce ? false : 'x'}
                  dragConstraints={{ left: -420, right: 420 }}
                  dragElastic={0.06}
                  dragMomentum={false}
                  onDragStart={reduce ? undefined : onDragStart}
                  onDrag={(e, info) => {
                    if (reduce) return;
                    rot.set(dragStartRotRef.current + info.offset.x * DEG_PER_PX);
                    pauseAuto();
                  }}
                  onDragEnd={reduce ? undefined : onDragEnd}
                />

                {/* ROTATING RING: ticks + major ticks + step nodes */}
                <motion.div
                  className="absolute"
                  style={
                    {
                      left: '50%',
                      top: geom.peakY,
                      width: geom.size,
                      height: geom.size,
                      x: -geom.size / 2, // ✅ only x via MotionValue (no translate-x utilities)
                      rotate: rot,
                      ['--dial' as any]: dialCss, // used by ticks to counter-rotate
                    } as any
                  }
                >
                  {/* minor/mid ticks */}
                  {ticks.map((i) => {
                    const t =
                      geom.tickCount <= 1 ? 0.5 : i / (geom.tickCount - 1);
                    const a = geom.tickStart + t * geom.tickSpan;

                    const major = i % geom.majorEvery === 0;
                    const mid = i % geom.midEvery === 0;

                    const L = major
                      ? geom.tickLen.major
                      : mid
                        ? geom.tickLen.mid
                        : geom.tickLen.minor;

                    const op = major ? 0.3 : mid ? 0.2 : 0.14;

                    return (
                      <Tick
                        key={`tick-${i}`}
                        a={a}
                        radius={geom.radius}
                        len={L}
                        opacity={op}
                      />
                    );
                  })}

                  {/* major ticks (1 per step) – rotates with ring, highlight when centered */}
                  {resolvedSteps.map((_, i) => (
                    <MajorTick
                      key={`major-step-tick-${i}`}
                      angleDeg={geom.stepAngles[i] ?? 0}
                      radius={geom.radius}
                      len={majorTickLen}
                      rot={rot}
                      thresholdDeg={DIAL.THRESHOLD_DEG}
                    />
                  ))}

                  {/* step nodes (icon + short title – all visible) */}
                  {resolvedSteps.map((s, i) => {
                    const stemLen = geom.isMobile ? 108 : 126;
                    return (
                      <StepNode
                        key={`step-node-${i}`}
                        angleDeg={geom.stepAngles[i] ?? 0}
                        radius={geom.radius}
                        stemLen={stemLen}
                        step={s}
                        index={i}
                        rot={rot}
                        reduce={!!reduce}
                        isMobile={geom.isMobile}
                        onPick={() => goTo(i)}
                      />
                    );
                  })}
                </motion.div>

                {/* CENTER MARKER (fixed) – NO fixed gold line, NO extra gold dot/diamond */}
                <div aria-hidden className="pointer-events-none absolute inset-0">
                  {/* top label */}
                  <div
                    className="absolute left-1/2 text-xs font-semibold tracking-wide text-white/70"
                    style={{
                      top: marker.labelY,
                      transform: 'translateX(-50%)',
                    }}
                  >
                    {pad2(displayIndex + 1)}
                  </div>

                  {/* center icon + title */}
                  <div
                    className="absolute left-1/2"
                    style={{ top: marker.iconY, transform: 'translateX(-50%)' }}
                  >
                    <displayStep.icon
                      aria-hidden
                      className="h-7 w-7 text-white/85"
                    />
                  </div>
                  <div
                    className="absolute left-1/2 px-6 text-center text-lg font-semibold tracking-tight text-white"
                    style={{
                      top: marker.titleY,
                      transform: 'translateX(-50%)',
                    }}
                  >
                    {displayStep.title}
                  </div>
                </div>
              </div>

              {/* arrows (wrap) */}
              <div className="mt-4 flex items-center justify-center gap-3">
                <button
                  type="button"
                  onClick={() => goBy(-1)}
                  className={[
                    'inline-flex h-10 w-10 items-center justify-center rounded-full',
                    'bg-white/10 text-white/80',
                    'shadow-[inset_0_0_0_1px_rgba(255,255,255,0.08),0_16px_34px_rgba(0,0,0,0.35)]',
                    'hover:bg-white/12 hover:text-white',
                    'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
                  ].join(' ')}
                  aria-label="Előző lépés"
                >
                  <ChevronLeft className="h-5 w-5" aria-hidden />
                </button>
                <button
                  type="button"
                  onClick={() => goBy(1)}
                  className={[
                    'inline-flex h-10 w-10 items-center justify-center rounded-full',
                    'bg-white/10 text-white/80',
                    'shadow-[inset_0_0_0_1px_rgba(255,255,255,0.08),0_16px_34px_rgba(0,0,0,0.35)]',
                    'hover:bg-white/12 hover:text-white',
                    'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
                  ].join(' ')}
                  aria-label="Következő lépés"
                >
                  <ChevronRight className="h-5 w-5" aria-hidden />
                </button>
              </div>

              {/* description + CTA (no crossfade; just updates with displayIndex) */}
              <div className="mt-6 flex flex-col items-center text-center">
                <div className="mx-auto max-w-2xl text-sm leading-relaxed text-white/58">
                  {displayStep.description}

                  {displayStep.ctaText && displayStep.ctaHref ? (
                    <div className="mt-5">
                      <motion.a
                        href={displayStep.ctaHref}
                        className={[
                          'inline-flex items-center gap-2 rounded-full',
                          'bg-white/10 px-5 py-2 text-sm text-white/85',
                          'shadow-[inset_0_0_0_1px_rgba(255,255,255,0.08),0_16px_34px_rgba(0,0,0,0.35)]',
                          'hover:bg-white/12 hover:text-white',
                          'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-white/25',
                        ].join(' ')}
                        whileHover={reduce ? undefined : { y: -1 }}
                        whileTap={reduce ? undefined : { y: 0, scale: 0.99 }}
                        transition={
                          reduce
                            ? undefined
                            : { type: 'spring', stiffness: 300, damping: 24 }
                        }
                      >
                        {displayStep.ctaText}
                        <span aria-hidden className="text-white/60">
                          →
                        </span>
                      </motion.a>
                    </div>
                  ) : null}
                </div>
              </div>

              {/* SR mapping */}
              <div className="sr-only" aria-hidden>
                {resolvedSteps.map((s, i) => (
                  <span key={`sr-step-${i}`}>{s.title}</span>
                ))}
              </div>
            </div>
          </div>
        </div>
      </Container>
    </section>
  );
};
